@font-face {
  font-family: 'hecho a mano';
  src:url('fonts/HechoAMano-Regular.ttf') format('ttf');
}

@font-face {
  font-family: 'Recipety';
  src:url('fonts/Recipety - Italic.woff') format('woff');
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.heart {
  color: red;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


@media only screen and (min-width: 850px) {
  body {
    margin: 0 auto;
    width: 850px;
  }
  .static-width {
    margin: 0 auto;
    width: 650px;
  }
}
